import React from 'react'
import Button from '@material-ui/core/Button'
// import AddIcon from '@material-ui/icons/Add'
import { useState } from 'react';
import './App.css';


function Note(props) {
  let [note, setNote] = useState({
    title: '',
    content: '',
  })


  // let [expand,setExpand]=useState(false)
  const [search, setSearch] = useState("");

  let addNote = (e) => {
    let {value,name} = e.target
    setNote((prev)=>{
     return { ...prev, [name]:value}
    })
    

  }
  let addEvent  = ()=>{
       props.passNote(note)
       setNote({
        title: '',
        content: '',
      })
  }
// let expandIt = ()=>{
//   setExpand(true)
// }



  return (
    <>
      <div className="box">
         
        <form className="form__group field">
          
          
          <input type="text" value={note.title} name="title" onChange={addNote}  autoComplete = "off" className="form__field" placeholder="enter the title"  />
          
          <label className="form__label">Title</label> 
          <input height = "10"  cols="10" onChange={addNote} name="content" className="text-area" maxLength="256" value={note.content} rows="10" placeholder="write a note"></input>
          < Button style={{height: "3rem", fontsize: "1.3rem"}} onClick ={addEvent}> Add Note </Button>
        </form>
          <input className="search" type = "text" placeholder="Search Tasks" onChange={(e)=>setSearch(e.target.value)} />
        {props.passSearch(search)}
        
        

      </div>
    </>
  )
}

export default Note