import React from 'react';

import './App.css';
import Header from './Header';
import Footer from './Footer';

import Note from './Note';
import AddNote from './AddNote'
import { useState, useEffect } from 'react';
// import Nav from './nav'

function App(props) {

  const {handleLogout} = props;

  let [addItem,setaddItem]= useState([])
  const [searched, setSearched] = useState('')
  const [filteredList, setFilteredList] = useState([""]);

  
  
  
  const filter = (search) =>{
    setSearched(search)
  }
  // const filteredResults = addItem.filter((val) =>{
  //   return addItem.includes(searched)
  // });   
  
  // console.log(filteredResults)

  const addNote = (note)=>{

    setaddItem((prev)=>{
      return [...prev,note]
    })
    
    
    
  }
  const onDelete = (id)=>{

    setaddItem((oldData)=>
      oldData.filter((curData,indx)=>{
        return indx !== id; 
      }
      
      )
    

    );

  }

  useEffect(() => {
    setFilteredList(
      addItem.filter((val) =>{
        return(

         val.title.toLowerCase().includes(searched.toLowerCase())
        )
        })
    );
  }, [searched, addItem]);
  console.log(setFilteredList)

useEffect(()=>{
  var x = document.querySelectorAll(".active")
  return console.log(x)
} )

// console.log(document.querySelectorAll(".active"))

  // const Active = () =>{
  //   addItem
  // }

  return (
    <>

        <section className = "hero">
            <nav>
                <h2>To-do List</h2>
                <button onClick = {handleLogout}>Log-Out</button>
            </nav>
        </section>
      {/* <Header /> */}
      {/* <Nav/> */}
      <Note passNote = {addNote}
      passSearch = {filter}
      />
      
      {
        filteredList.map((val,index)=>{
          return ( <AddNote 
            key= {index}
            id={index}
            title={val.title}
            content={val.content}
            deleteItem ={onDelete}
         />)
          
        })
      }
      {/* {searched} */}
      <Footer />
    </>
  );
}

export default App;